*,
*:after,
*:before {
  box-sizing: border-box;
  outline: none;
  font-family: 'Inter', -apple-system, system-ui, sans-serif; }

body {
  margin: 0;
  font-family: 'Inter', -apple-system, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E7F6FF;
  position: relative; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

#root {
  min-height: 100vh; }

.wrapper {
  background-color: #fff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 1.25em;
  max-width: (1080em / 16);
  margin: auto; }


.content {
  padding: (44em/16) (60em/16);

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2.25em; } }

.main-title {
  font-size: 2em;
  margin-bottom: 0;
  margin-top: 0; }

.cards {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 30px 40px;
  grid-template-areas: ". . . ."; }

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;
  position: relative;
  display: flex;
  align-items: center;

  .removeBtn {
    position: absolute;
    right: 0;
    width: 18px;
    height: 18px;
    top: 14px;
    right: 15px; }

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    width: 200px; } }
